.message-sent {
  background-color: #2b4246;
  border-radius: 10px;
  height: max-content;
  padding: 25px;
  position: fixed;
  top: 0;
  width: max-content;
}
@media (max-width: 767.98px) {
  .message-sent {
    width: 250px;
  }
}
.message-sent .title {
  color: hsl(0, 0%, 100%);
  display: flex;
  gap: 10px;
}
.message-sent .title h2 {
  font-size: 16px;
  align-self: center;
}
.message-sent .title svg {
  justify-self: center;
}
.message-sent p {
  color: rgba(255, 255, 255, 0.75);
  margin-top: 15px;
}

* {
  font-family: "Karla", sans-serif;
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  display: grid;
  justify-items: center;
  align-items: center;
  background-color: hsl(148, 38%, 91%);
}

form {
  align-self: center;
  margin: 100px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  min-height: max-content;
  /* justify-content: center; */
  padding: 20px 40px;
  width: 735px;
}
@media (max-width: 767.98px) {
  form {
    margin: 20px;
    width: 345px;
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: hsl(0, 0%, 100%);
    padding: 20px;
    border-radius: 10px;
  }
}
form h2 {
  font-size: 24px;
  font-weight: bold;
  color: hsl(187, 24%, 22%);
  margin: 20px 0px;
}
form .input-container {
  margin: 15px 0px;
  display: flex;
  gap: 15px;
}
@media (max-width: 767.98px) {
  form .input-container {
    display: flex;
    flex-direction: column;
  }
}
form .input-container .first {
  display: grid;
}
form .input-container .second {
  display: grid;
}
form input:hover, form textarea:hover, form .input-query:hover {
  border: 1px solid hsl(169, 82%, 27%);
}
form input:focus, form textarea:focus, form .input-query:focus {
  outline: 1px solid hsl(169, 82%, 27%);
  background: rgba(223.329, 240.771, 231.4686, 0.5);
}
form input:active, form textarea:active, form .input-query:active {
  outline: 1px solid hsl(169, 82%, 27%);
  background: rgba(223.329, 240.771, 231.4686, 0.5);
}
form .input + #query {
  margin-top: 15px;
}
form input, form textarea {
  cursor: pointer;
  margin-top: 10px;
  border: 1px solid hsl(186, 15%, 59%);
  border-radius: 10px;
  padding: 25px;
}
form input[type=text], form .input-query {
  width: 320px;
  height: 50px;
}
@media (max-width: 767.98px) {
  form input[type=text], form .input-query {
    width: 300px;
  }
}
form input[type=email] {
  height: 50px;
}
@media (max-width: 767.98px) {
  form input[type=email] {
    width: 300px;
  }
}
form .input-query {
  display: flex;
  gap: 10px;
  justify-content: left;
  border: 1px solid hsl(186, 15%, 59%);
  border-radius: 10px;
  padding: 15px 30px;
}
form .input-query p {
  align-self: end;
}
form input[type=radio] {
  align-self: end;
  width: 20px;
  height: 20px;
  border: 1px solid hsl(148, 38%, 91%);
  accent-color: hsl(169, 82%, 27%);
}
form input[type=checkbox] {
  width: 20px;
  height: 20px;
  accent-color: hsl(169, 82%, 27%);
}
form textarea {
  width: 655px;
  height: 105px;
}
@media (max-width: 767.98px) {
  form textarea {
    width: 300px;
  }
}
form .agree-container {
  margin: 40px 0;
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 2px;
}
form .agree-container input {
  align-self: center;
}
form .agree-container p {
  align-self: end;
}
form button[type=submit] {
  cursor: pointer;
  width: 655px;
  height: 60px;
  background-color: hsl(169, 82%, 27%);
  color: hsl(0, 0%, 100%);
  font-size: 16px;
  font-weight: bold;
  border: 0;
  border-radius: 10px;
}
@media (max-width: 767.98px) {
  form button[type=submit] {
    width: 300px;
  }
}
form button[type=submit]:hover, form button[type=submit]:focus {
  background-color: hsl(187, 24%, 22%);
}